.chat_wrap {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 320px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    z-index: 999;
  }
  
  .chat_wrap .header {
    background-color: #28a745;
    color: #fff;
    padding: 12px;
    font-size: 16px;
    font-weight: bold;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }
  
  .chat_wrap .chat {
    overflow-y: scroll;
    max-height: 250px;
  }
  
  .chat_wrap .chat ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .chat_wrap .chat li {
    margin: 10px;
    padding: 10px;
    border-radius: 8px;
    background-color: #f2f2f2;
  }
  
  .chat_wrap .chat li:nth-child(even) {
    background-color: #ddd;
  }
  
  .chat_wrap .chat .sender {
    font-size: 14px;
    color: #aaa;
    margin-bottom: 4px;
  }
  
  .chat_wrap .input-div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
  }
  
  .chat_wrap .input-div textarea {
    width: calc(100% - 70px);
    height: 50px;
    resize: none;
    font-size: 14px;
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: #f2f2f2;
    margin-right: 10px;
  }
  
  .chat_wrap .input-div button {
    width: 60px;
    height: 50px;
    border-radius: 5px;
    border: none;
    background-color: #28a745;
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
  }